.card {
  font-family: Montserrat, "Helvetica Neue", Helvetica, Arial, sans-serif;
  padding: 20px;
  box-shadow: 9px 9px 4px 0px #ccc;
  background-color: #0e143c;
  color: #fff;
  flex-direction: column;
  margin-bottom: 10px;
  margin-top: 10px;
  margin-left: 30px;
  margin-right: 30px;
}

.img {
  width: 70%;
  height: 60%;
}

.full-img {
  width: 100%;
  height: 100%;
}

.technologies {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
}

.label {
  font-weight: 600;
}

.tag {
  background-color: #3880ab;
  padding: 5px;
  border-radius: 5px;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 7px;
}

